.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.502);
    z-index: 11;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wrapper {
    background-color: var(--white);
    width: 483px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    border-radius: 8px;
}

.btn {
    width: 100%;
    height: unset;
    height: 36px;
}

.close {
    position: absolute;
    top: 12px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    right: 12px;
    width: 36px;
    height: 36px;
    font-size: 1.8rem;
    background: #16182308;
}

.body {
    overflow-y: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 12px;
    width: 100%;
    height: 410px;
}

.header {
    color: #161823;
    font-family: 'SofiaPro';
    font-size: 32px;
    margin-top: 42px;
    margin-bottom: 24px;
    font-weight: bold;
    text-align: center;
}



.footer {
    width: 100%;
    color: #161823;
    font-family: var(--font-family);
    height: 64px;
    border-top: 1px solid #1618231f;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
    line-height: 18px;
}

.title {}

.change {
    color: var(--pr-cl);
}

// Item Login
.item {
    width: 375px;
    flex-shrink: 0;
    color: #161823;
    font-family: var(--font-family);
    font-weight: 600;
    border: 1px solid #1618231f;
    font-size: 15px;
    padding: 0px 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 44px;
    position: relative;
    cursor: pointer;
    margin-bottom: 16px;
    white-space: nowrap;
    background: #fff;
}

.item-title {
    flex: 1;
    text-align: center;
}