.wrapper {
    padding: 20px;
    min-width: 324px;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.avatar {
    width: 44px;
    height: 44px;
    object-fit: cover;
    border-radius: 50%;


}

.body {
    margin-top: 12px;
    color: #161823;
}

.nickname {
    font-size: 1.8rem;
    line-height: 1;
}

.tick-icon {
    color: #20d5ec;
    margin-left: 4px;
}

.name {
    font-size: 1.6rem;
    margin-bottom: 8px;
}

.analytics {
    font-size: 1.6rem;
}

.value~.value {
    margin-left: 20px;
}