.wrapper {
    --search-border-radius: 92px;
    --search-height: 46px;
    --search-top-spacer: 9px;
    --search-button-width: 52px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: var(--default-layout-header-height);
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    display: flex;
    justify-content: center;
    background-color: var(--white);
    z-index: 10;
}

.inner {
    height: 100%;
    width: var(--default-layout-width);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo-link {
    display: flex;
    align-items: center;
}



.action {
    display: flex;
    // align-items: center;

}

.more-btn {
    font-size: 2rem;
    margin-left: 12px;
    background: transparent;
    padding: 12px;
    cursor: pointer;
}

.action-btn {
    color: rgba(22, 24, 35);
    background-color: transparent;
    font-size: 2.2rem;
    padding: 0px 12px;
    cursor: pointer;
    position: relative;
}

.badge {
    position: absolute;
    top: -3px;
    right: 0px;
    padding: 0px 6px;
    height: 2rem;
    line-height: 2rem;
    border-radius: 10px;
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--white);
    font-family: var(--font-family);
    background-color: var(--pr-cl);
}

.user-avatar {
    width: 32px;
    height: 32px;
    object-fit: cover;
    margin-left: 12px;
    cursor: pointer;
    border-radius: 50%;
}